@import url(https://fonts.googleapis.com/css2?family=Major+Mono+Display&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Calistoga&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Hind&display=swap);

.pomodoro * {
  box-sizing: border-box;
}

.pomodoro {
  padding: 0;
  margin: 0;
  font-family: "Major Mono Display", monospace;
  line-height: 1.5;
}

.pomodoro .app {
  min-height: 100vh;
  padding-left: 20px;
  padding-right: 20px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  background: #87d7cc;
  color: #2c888d;
}

.pomodoro h2 {
  color: #49b2b8;
  font-size: 30px;
  text-align: center;
  margin: 0;
}

.pomodoro .timer {
  color: #1f6e72;
  font-size: 150px;
  margin-bottom: 20px;
}

.pomodoro .buttons button {
  outline: none;
  border: none;
  font-size: 24px;
  background: #f7fca0;
  color: #878b32;
  border-radius: 5px;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
  padding: 15px 30px;
  cursor: pointer;
  transition: 0.3s ease all;
}

.pomodoro .buttons button:hover {
  border-radius: 10px;
  background: #f5f8ca;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.15);
  -webkit-transform: translateY(-2px);
          transform: translateY(-2px);
}

.pomodoro .buttons button:not(:last-child) {
  margin-right: 10px;
}

@media (max-width: 780px) {
  .pomodoro h2 {
    font-size: 22px;
  }

  .pomodoro .timer {
    font-size: 80px;
  }

  .pomodoro .buttons button {
    font-size: 18px;
  }
}

.auth * {
  box-sizing: border-box;
}

.auth {
  margin: 0;
  padding: 0;
}

.auth h1,
.auth h2,
.auth h3,
.auth h4,
.auth h5,
.auth h6 {
  font-family: "Calistoga", cursive;
}

.auth .app {
  cursor: url("https://i.imgur.com/NBIhMDc.png"), auto;
}

.auth .site-header {
  margin-bottom: auto;
  /*position: fixed;*/
  width: 100%;
  left: 0;
  right: 0;
  top: 0;
  text-align: center;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  padding: 10px 20px;
  background: rgba(255, 255, 255, 0.2);
}

.auth .site-header a,
.auth .site-header button {
  display: inline-block;
  padding: 4px 8px;
  font-size: 18px;
  font-family: "Calistoga", cursive;
  color: #333;
  margin-right: 8px;
  text-decoration: none;
  background: none;
  border: none;
  cursor: pointer;
}

.auth .site-header a:hover,
.auth .site-header button:hover {
  color: rgb(31, 132, 80);
}

.auth .page {
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  background-color: #104826;
  background-image: url("https://images.unsplash.com/photo-1505990941976-fa5c0b5a43f1?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1950&q=80");
  background-size: cover;
}

.auth .page > div {
  text-align: center;
  position: relative;
}

.auth .page h2 {
  background: rgba(255, 255, 255, 0.9);
  padding: 15px 30px;
  border-radius: 50px;
  position: absolute;
  color: #25b75f;
  -webkit-transform: rotate(-5deg) translateY(50px);
          transform: rotate(-5deg) translateY(50px);
  width: 100%;
  bottom: 0;
  left: 0;
  font-size: 60px;
  line-height: 1;
}

.auth .dashboard {
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  background-color: #104826;
  background-image: url("https://images.unsplash.com/photo-1506466010722-395aa2bef877?ixlib=rb-1.2.1&auto=format&fit=crop&w=2376&q=80");
  background-size: cover;
}

.markdown {
  background: #e8eaf1;
  font-family: 'Hind', Arial, Helvetica, sans-serif;
}

.markdown .app {
  min-height: 100vh;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 15px;
}

.markdown textarea {
  background: #eff1f5;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
  outline: none;
  border: none;
  border-radius: 5px;
  padding: 20px;
  font-size: 22px;
}

.markdown .preview {
  padding-left: 20px;
  padding-right: 20px;
}

.markdown blockquote {
  font-size: 30px;
  background: #efefef;
  border-left: 12px solid rgb(44, 163, 241);
  padding: 20px 30px;
  border-radius: 5px;
  margin: 0;
}

